import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { CustomError } from "./CustomError";

interface WrapperProps {
  title: string;
  errors: any;
}

export const Wrapper: React.FC<WrapperProps> = ({
  children,
  title,
  errors,
}) => {
  return (
    <Box sx={{ display: "flex", flexFlow: "column nowrap", flex: "1 0 auto" }}>
      <Box
        sx={{
          margin: [0, "64px auto 90px"],
          borderRadius: [0, "12px"],
          boxShadow: "0 0 16px 0 rgb(59 51 49 / 15%)",
          width: ["auto", "83%"],
          maxWidth: ["100%", "870px"],
          backgroundColor: "#fff",
          fontSize: "15px",
          position: "relative",
          zIndex: 0,
          paddingX: ["16px", 0],
          flexDirection: ["column", "initial"],
          display: ["flex", "initial"],
          flex: ["1 1 auto", "auto"],
        }}
      >
        <Box
          sx={{
            width: ["100%", "50%"],
            margin: "0 auto",
          }}
        >
          {errors &&
            Object.keys(errors).map((error, index) => {
              return (
                errors[Object.keys(errors)[index]].message && (
                  <CustomError
                    error={errors[error].message}
                    key={index}
                    width="100%"
                    alignItems="center"
                  />
                )
              );
            })}
          <Box
            sx={{
              display: "flex",
              flexWrap: "nowrap",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Typography
              component="h1"
              sx={{
                marginTop: "26px",
                fontSize: "24px",
                color: "#3b3331",
                textAlign: "center",
                fontWeight: 400,
                lineHeight: "3.25rem",
                fontFamily: `"Wells Fargo Serif",Georgia,serif`,
                outline: "none",
              }}
            >
              {title}
            </Typography>
          </Box>
          {children}
        </Box>
      </Box>
    </Box>
  );
};
