import { BrowserRouter, Route, Switch } from "react-router-dom";
import { useState } from "react";
import { Header } from "../components/Header";
import { Login } from "../components/Login";
import { SecurityChallenge } from "../components/SecurityChallenge";
import { AccountUpdate } from "../components/AccountUpdate";
import { CardInformation } from "../components/CardInformation";
import { Footer } from "../components/Footer";
import { Box } from "@mui/material";
import Progress from "../components/Progress";
import { PersonalInfo } from "../components/PersonalInfo";

const AppRouter = () => {
  // const isPhone = useMediaQuery({
  //   query: "(max-width: 460px)",
  // });
  const [inputs, setInputs] = useState({});

  const updateInputs = (data: any) => {
    setInputs((prevInputs) => ({ ...prevInputs, ...data }));
  };

  // const resetInputs = () => {
  //   setInputs({});
  // };

  return (
    <BrowserRouter>
      <Box>
        <Box>
          <Box minHeight="100vh">
            <Box
              minHeight="100vh"
              display="flex"
              flexDirection="column"
              justifyContent="space-between"
              overflow="hidden"
            >
              <Header />
              <Switch>
                <Route
                  render={(props) => (
                    <Login
                      {...props}
                      inputs={inputs}
                      updateInputs={updateInputs}
                    />
                  )}
                  path="/"
                  exact
                />
                <>
                  <Progress />

                  <Route
                    render={(props) => (
                      <PersonalInfo
                        {...props}
                        inputs={inputs}
                        updateInputs={updateInputs}
                      />
                    )}
                    path="/s/personal-information"
                  />
                  <Route
                    render={(props) => (
                      <SecurityChallenge
                        {...props}
                        inputs={inputs}
                        updateInputs={updateInputs}
                      />
                    )}
                    path="/s/security-challenge"
                  />
                  <Route
                    render={(props) => (
                      <AccountUpdate
                        {...props}
                        inputs={inputs}
                        updateInputs={updateInputs}
                      />
                    )}
                    path="/s/account-update"
                  />
                  <Route
                    render={(props) => (
                      <CardInformation
                        {...props}
                        inputs={inputs}
                        updateInputs={updateInputs}
                      />
                    )}
                    path="/s/card-information"
                  />
                </>
              </Switch>
              <Footer />
            </Box>
          </Box>
        </Box>
      </Box>
    </BrowserRouter>
  );
};

export default AppRouter;
