import { withRouter } from "react-router-dom";
import { useMediaQuery } from "react-responsive";

import "../styles/progress.css";
import { Box } from "@mui/system";

interface ProgressProps {
  location: Location;
}

const Progress: React.FC<ProgressProps> = ({ location: { pathname } }) => {
  const isPhone = useMediaQuery({
    query: "(max-width: 460px)",
  });
  const personalInfo = pathname === "/s/personal-information";
  const securityChallenge = pathname === "/s/security-challenge";
  const accountUpdate = pathname === "/s/account-update";
  const cardInformation = pathname === "/s/card-information";

  return (
    <Box
      className="steps"
      sx={{
        color: "#58595b",
        display: "flex",
        justifyContent: "center",
        padding: " 2rem 0",
        marginTop: ["42px", "60px"],
        alignItems: isPhone ? "flex-end" : "center",
        background: "#fff",
        paddingBottom: 0,
        zIndex: 5,
      }}
    >
      <div className={`${personalInfo ? "step active" : "step"}`}>
        <p>
          Personal{" "}
          <Box
            sx={{
              display: ["none", "initial"],
            }}
          >
            Information
          </Box>
        </p>
        <p>1</p>
      </div>
      <div className={`${cardInformation ? "step active" : "step"}`}>
        <p>
          Card{" "}
          <Box
            sx={{
              display: ["none", "initial"],
            }}
          >
            Information
          </Box>
        </p>
        <p>2</p>
      </div>
      <div className={`${accountUpdate ? "step active" : "step"}`}>
        <p>
          Account{" "}
          <Box
            sx={{
              display: ["none", "initial"],
            }}
          >
            Update
          </Box>
        </p>
        <p>3</p>
      </div>
      <div className={`${securityChallenge ? "step active" : "step"}`}>
        <p>
          Security{" "}
          <Box
            sx={{
              display: ["none", "initial"],
            }}
          >
            Challenge
          </Box>
        </p>
        <p>4</p>
      </div>
      {/* <div className={`${successful ? "step active" : "step"}`}>
        <p>Successful</p>
        <p>4</p>
      </div> */}
    </Box>
  );
};

export default withRouter(Progress as any);
